@import '../App.css';

.overlay-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1000px;
  margin: 20px auto;
  padding: 20px;
  background-color: #9a9cb3;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  overflow: hidden;
  position: relative;
}

.card-image {
  align-self: center;
  object-fit: cover;
  max-width: 400px;
  max-height: 400px;
  border-radius: 8px;
}

.card-content {
  padding: 20px;
  text-align: left;
  color: #333;
  font-family: Arial, sans-serif;
}

.card-content p {
  font-size: 1.2rem;
}
