@import '../App.css';

* {
    margin: 0;
    padding: 0;
    font-family: "Quicksand";
    box-sizing: border-box;
}

.bg {
    color: rgb(36, 36, 43);
}

.timeline {
    position: relative;
    max-width: 1200px;
    margin: 100px auto;
}

.container {
    padding: 10px 50px;
    margin: 10px;
    position: relative;
    width: 50%;
    border-radius: 12px;
}

.textbox {
    padding: 20px 30px;
    background-color: rgb(79, 91, 133);
    color: rgb(190, 190, 197);
    border-radius: 12px;
    font-size: 15px;
}

.left-container {
    left: 0;
}

.right-container {
    left: 50%;
}

.container img {
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    right: -20px;
    top: 32px;	
    z-index: 10;
}

.right-container img {
    left: -20px;
}

.timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    height: 100%;
    background: rgb(79, 91, 133);
    top: 0;
    left: 50%;
    margin-left: 7px;
    z-index: -1;
}

.textbox h1{
    font-weight: 600;
}

.textbox small {
    display: inline block;
    margin-bottom: 15px;
}