@import '../App.css';

/* Style the navbar */
.navbar {
    display: flex;
    align-items: center; /* Vertically center items */
    justify-content: space-between; /* Distribute items across the navbar */
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: #20222e; /* Set background color to grey */
  }
  
  /* Adjust spacing between list elements dynamically */
  .navbar li {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 20px 0 20px; /* 20px horizontal padding */
    padding-left: 2vw;
    padding-right: 2vw;
    height: 100%;
  }
  
  /* Style the navbar links */
  .navbar li a {
    font-family: 'Nexa Hv';
    text-decoration: none;
    color: #ffffff;
    font-weight: bold;
    font-size: 18px;
  }
  
  /* Adjust the logo size */
  .logo img {
    height: 50px;
    width: auto;
  }
  
  .navbar li a:hover {
    text-decoration: underline;
  }
  